<template>
  <div id="app">
  </div>
</template>

<script>

  import {userIsLogin} from "./user/user";

  console.log()

  export default {
    name: 'App',
    created:function () {

      let isLogin= userIsLogin();

      if (!isLogin){

        window.location.href='login.html';

      }
      else {
        window.location.href='admin.html';
      }
    }
  }
</script>


<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



